import React from "react";
import useStyles from '../../styles/usersStyle'
import {
    Box,
    Button,
    Card,
    CardActionArea,
    CardActions,
    CardContent,
    CardMedia,
    Container,
    Grid,
    Typography
} from "@material-ui/core";
import user from '../../img/download.png'
import UserPagination from "../Pagination/UserPagination";
import {NavLink} from "react-router-dom";

const UsersCard = (props) => {

    const classes = useStyles()

    return (
        <div>
            <Container maxWidth={"lg"}>
                <h1>Users</h1>
                <UserPagination
                    totalUsersCount={props.totalUsersCount}
                    usersPageSize={props.usersPageSize}
                    usersCurrentPage={props.usersCurrentPage}
                    onChangePage={props.onChangePage}
                />
                <Grid container spacing={3}>
                    {props.usersData.usersData.map( (el, idx) => {

                        return (
                            <div className={classes.root} key={idx}>
                                <Box boxShadow={3}>
                                    <Grid item >
                                        <Card className={classes.card}>
                                            <CardActionArea>
                                                <CardMedia
                                                    className={classes.media}
                                                    image={el.photos.small
                                                            ? el.photos.small
                                                            : user
                                                    }
                                                    title={el.name}
                                                />
                                                <CardContent>
                                                    <Typography gutterBottom variant="h5" component="h2">
                                                        <NavLink className={classes.userLink} activeClassName="active"  to={`/profile/${el.id}`}>
                                                            {el.name}
                                                            &nbsp;
                                                        </NavLink>
                                                    </Typography>
                                                </CardContent>
                                            </CardActionArea>
                                            <CardActions>
                                                {
                                                    el.completed
                                                        ? <Button disabled={props.isButtonProgressing.some( id => id === el.id)} color={"primary"} onClick={() => {
                                                            props.followApi(el.id)
                                                        }}>Follow</Button>
                                                        : <Button disabled={props.isButtonProgressing.some( id => id === el.id)} color={"secondary"} onClick={() => {
                                                            props.unfollowApi(el.id)
                                                        }}>Unfollow</Button>
                                                }
                                            </CardActions>
                                        </Card>
                                    </Grid>
                                </Box>
                            </div>
                        )
                    })}
                </Grid>
            </Container>
        </div>
    )
}

export default UsersCard